html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "PT Sans", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body.fijar-body {
    overflow: hidden;
  }
  body.body-scroll {
    padding-top: 8.6rem;
  }
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.centrar-texto {
  text-align: center;
}

.header {
  background-color: #112D4E;
}
.header h1 {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  font-size: 4rem;
}
@media (min-width: 992px) {
  .header h1 {
    margin: 1rem 0;
  }
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.4rem #4d4c4c;
  }
}

.contenido-header {
  padding: 2rem;
}
@media (min-width: 992px) {
  .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    justify-content: center;
    gap: 2rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  display: block;
  font-size: 2.4rem;
  text-align: center;
  margin-bottom: 1rem;
}

.video {
  position: relative;
  max-height: 80rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(17, 45, 78);
  background: linear-gradient(153deg, rgba(17, 45, 78, 0.6839110644) 0%, rgba(63, 114, 175, 0.5410539216) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 10;
}
.contenido-video h2 {
  color: #FFFFFF;
  font-size: 7rem;
  margin: 0;
}
.contenido-video p {
  color: #112D4E;
  font-size: 2.4rem;
  margin: 0;
  font-weight: bold;
  background-color: rgba(249, 247, 247, 0.9);
  padding: 1rem 2rem;
  display: inline-block;
}

.sobre-mi {
  background-color: #a1b6dd;
}

.contenedor-sobre-mi {
  padding: 7rem 0;
  align-items: center;
}
@media (min-width: 768px) {
  .contenedor-sobre-mi {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2rem;
  }
}
@media (min-width: 992px) {
  .contenedor-sobre-mi {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.contenedor-sobre-mi h3 {
  margin-bottom: 0;
}
.contenedor-sobre-mi p {
  line-height: 1.6;
}
.contenedor-sobre-mi .info {
  text-align: center;
}
.contenedor-sobre-mi .contenedor-skills img {
  margin: 0 auto;
}
@media (min-width: 320px) {
  .contenedor-sobre-mi .contenedor-skills {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    margin-bottom: 5rem;
  }
  .contenedor-sobre-mi .contenedor-skills img {
    width: 45%;
  }
}
@media (min-width: 768px) {
  .contenedor-sobre-mi .contenedor-skills img {
    width: 53%;
  }
}
@media (min-width: 992px) {
  .contenedor-sobre-mi .contenedor-skills {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
  .contenedor-sobre-mi .contenedor-skills img {
    width: 53%;
  }
}

.proyectos-fondo {
  background-color: #cdd9f5;
}

.proyectos {
  padding: 5rem 0;
}
.proyectos h2 {
  font-size: 5rem;
}

@media (min-width: 320px) {
  .galeria-proyectos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-proyectos {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-proyectos {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.galeria-proyectos a {
  -webkit-box-shadow: -2px 6px 20px -6px rgba(0, 0, 0, 0.59);
  -moz-box-shadow: -2px 6px 20px -6px rgba(0, 0, 0, 0.59);
  box-shadow: -2px 6px 20px -6px rgba(0, 0, 0, 0.59);
}

.contacto {
  background-color: #eff3ff;
}
.contacto h3 {
  padding-top: 2rem;
}

.contacto-bg {
  background-image: url(../img/contact.jpg);
  height: 40rem;
  background-repeat: no-repeat;
}
@media (min-width: 320px) {
  .contacto-bg {
    background-position: right center;
  }
}
@media (min-width: 768px) {
  .contacto-bg {
    background-position: bottom center;
    background-size: cover;
  }
}

.formulario {
  background-color: #eff3ff;
  margin: -12rem auto 0 auto;
  width: 95%;
  padding: 5rem;
}

@media (min-width: 320px) {
  .campo {
    display: block;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .campo {
    display: flex;
    margin-bottom: 2rem;
    gap: 2rem;
  }
}

@media (min-width: 320px) {
  .campo__label {
    display: block;
    text-align: left;
  }
}
@media (min-width: 992px) {
  .campo__label {
    flex: 0 0 9rem;
    text-align: right;
    line-height: 2;
  }
}

.campo__field {
  flex: 1;
  border: 1px solid #ccc7c7;
}
@media (min-width: 320px) {
  .campo__field {
    width: 100%;
  }
}

.campo__field--textarea {
  height: 10rem;
}
@media (min-width: 320px) {
  .campo__field--textarea {
    height: 20rem;
  }
}

.boton {
  padding: 1rem 2rem;
  background-color: #112D4E;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 2rem;
}
.boton:hover {
  cursor: pointer;
}

.footer {
  display: flex;
  justify-content: space-around;
  background-color: #112D4E;
  padding: 0.5rem;
}
.footer .info-footer {
  display: flex;
  gap: 2rem;
}
.footer p {
  color: #FFFFFF;
  font-weight: bold;
  margin: 0;
}
@media (min-width: 320px) {
  .footer p {
    font-size: 3rem;
  }
}
@media (min-width: 768px) {
  .footer p {
    font-size: 4rem;
  }
}
@media (min-width: 320px) {
  .footer img {
    width: 5rem;
    height: 5rem;
  }
}
@media (min-width: 768px) {
  .footer img {
    width: 7rem;
    height: 7rem;
  }
}